    .title__second {
        font-family: $secondary-font;
        font-weight: 400;
        font-size: 18px;
        line-height: calc(21 / 18);
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 15px;
        color: $secondary-accent-color;
        @media screen and(min-width: 768px) {
            font-size: 26px;
            line-height: calc(30 / 26);
            margin-bottom: 16px;
        }
        @media screen and(min-width:1280px) {
            font-size: 30px;
            line-height: calc(34 / 30);
            letter-spacing: 0.04em;
        }
    }
    
    .title__main {
        width: 200px;
        font-family: $secondary-font;
        font-weight: 400;
        font-size: 34px;
        line-height: 1.15;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 26px;
        color: $primary-accent-color;
        @media screen and(min-width: 768px) {
            width: 429px;
            margin-bottom: 50px;
            font-size: 47px;
            line-height: calc(55 / 47);
        }
        @media screen and(min-width:1280px) {
            width: 534px;
            margin-bottom: 55px;
            font-size: 58px;
            line-height: calc(66 / 58);
        }
    }
    
    .about {
        background-color: $secondary-bg-color;
        @media screen and (min-width: 320px) {
            background-image: url('../images/mobile/about/milk-mobile.png');
            background-repeat: no-repeat;
            background-position-y: bottom;
            background-size: contain;
        }
        @media screen and (min-width: 768px) {
            background-image: none;
        }
        @media screen and (min-width: 1279px) {
            background-image: url('../images/desktop/about/milk-desktop.png');
            background-repeat: no-repeat;
            background-size: contain;
            background-position-y: bottom;
        }
    }
    
    .about-container {
        padding-top: 125px;
        padding-bottom: 40px;
        @media screen and (min-width: 768px) {
            padding-top: 130px;
            padding-bottom: 60px;
        }
        @media screen and (min-width: 1280px) {
            padding-top: 125px;
            padding-bottom: 40px;
        }
    }
    
    .about-position {
        display: block;
        @media screen and (min-width: 768px) {
            display: flex;
        }
    }
    
    .content__img {
        display: block;
        width: 280px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 46px;
        @media screen and(min-width: 768px) {
            width: 340px;
            height: 340px;
            margin-right: 17px;
        }
        @media screen and(min-width:1280px) {
            width: 516px;
            height: 516px;
            margin-left: 112px;
            margin-right: 74px;
        }
    }
    
    .content__text--wrap {
        justify-content: center;
        @media screen and(min-width: 768px) {
            padding-top: 50px;
        }
    }
    
    .content__main {
        font-family: $main-font;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.62;
        margin: 0 auto;
        margin-bottom: 47px;
        width: 220px;
        height: 80px;
        color: $primary-text-color;
        @media screen and(min-width: 768px) {
            font-size: 14px;
            line-height: 1.76;
            width: 340px;
        }
        @media screen and(min-width:1280px) {
            font-size: 16px;
            line-height: 1.76;
            width: 463px;
            letter-spacing: 0.04em;
        }
    }
    
    .content__second {
        font-family: $main-font;
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 1.62;
        width: 220px;
        height: 155px;
        margin: 0 auto;
        margin-bottom: 50px;
        color: $secondary-text-color;
        @media screen and(min-width: 768px) {
            width: 340px;
            font-weight: 500;
            font-size: 14px;
            line-height: 1.76;
            letter-spacing: 0.04em;
        }
        @media screen and(min-width:1280px) {
            font-size: 14px;
            line-height: 1.94;
            width: 463px;
        }
    }
    
    .content__second--wrap {
        @media screen and(max-width: 767px) {
            margin-left: -20px;
            margin-right: -20px;
            background-image: url(/images/mobile/about/milk-mobile.png);
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
    }
    
    .content__btn {
        justify-content: center;
        display: flex;
        align-items: center;
        width: 176px;
        height: 44px;
        background: $primary-bg-color;
        border-radius: 44px;
        border-color: transparent;
        font-family: $main-font;
        color: $primary-accent-color;
        font-weight: 700;
        font-size: 16px;
        line-height: 1.31;
        transition: background-color 250ms cubic-bezier(0.17, 0.67, 0.83, 0.67);
        &:hover {
            background-color: $hover-button-menu-color;
        }
        @media screen and(max-width:767px) {
            margin: 0 auto;
        }
    }
    
    .content__btn--icon {
        margin-left: 14px;
        width: 5px;
        height: 8px;
    }