.header {
    background-color: $hero-header-bg;
    @media screen and (min-width:1280px) {
      padding-top: 37px;
    }
  }
  .container--header {
    position: relative;
    display: flex;
    align-items: center;
    z-index: 2;
    @media screen and (min-width:768px) and (max-width:1279px){
      padding: 0 34px 0 0;
      left: 34px;
      top: 37px;
      justify-content: space-between;
    }
    @media screen and (min-width: 1280px){
      padding-left: 46px;
    }
  }
  .container--hero{
    height: 100%;
    position: relative;
  }
  .container-picture {
    min-width: 199px;
    min-height: 50px;
    @media screen and (min-width:480px) and (max-width:768px) and (max-width:1279px){
    margin-right: 61px;
    }
  }
  .main-nav {
    margin-left: 110px;
    margin-right: 110px;
    @media not screen and (min-width:1280px) {
      display: none;
    }
  }
  .main-nav__container {
    @media screen and (min-width: 480px) and (max-width: 1199px) {
      display: block;
    }
  }
  .main-nav__list {
    display: flex;
    align-items: center;
  }
  .main-nav__item {
    margin-right: 35px;
    &:last-child {
      margin-right: 0;
    }
  }
  .main-nav__link {
    font-family: $main-font;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(21 / 16);
  
    color: $menu-text-color;
    &:hover,
    &:focus {
      color: $primary-accent-color;
    }
  }
  .header__btn-buy {
    background: $primary-bg-color;
    border-radius: 22px;
    border-color: transparent;
    min-width: 160px;
    height: 44px;
    margin-left: 30px;
  
    font-family: $main-font;
    color: $primary-accent-color;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(21 / 16);
    @media screen and (max-width: 768px) {
      display: none;
    }
    @media screen and (min-width:1280px) {
      margin-left: auto;
      min-width: 159px;
    }
    &:focus,
    &:hover {
      background: #ecebeb;
    }
  }
  .modal__button-open {
    background-color: transparent;
    border-color: transparent;
    @media screen and (min-width:1280px) {
      display: none;
    }
  }
  .modal__button-open-icon {
    width: 20px;
    height: 12px;
  }
  .modal-hero{
    background-color: #e17992;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: 9;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
    @media not screen and (max-width: 1280px) {
      display: none;
    }
  }
  
  .modal-hero.is-hidden {
    display: none;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
  .modal__button-close {
    position: absolute;
    background-color: transparent;
    border-color: transparent;
    top: 23px;
    right: 28px;
  }
  .modal__button-close-icon {
    width: 14px;
    height: 14px;
  }
  .modal-hero__contaiter {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
  }
  .modal__btn-buy {
    background: $primary-bg-color;
    border-radius: 22px;
    border-color: transparent;
    width: 209px;
    height: 44px;
    margin-left: 21px;
    margin-right: 20px;
  
    font-family: $main-font;
    color: $primary-accent-color;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(21 / 16);
    @media screen and (min-width: 768px) {
      width: 186px;
    }
    &:focus,
    &:hover {
      background: #ecebeb;
    }
  }
  .modal__btn-buy-arrow {
    margin-left: 14px;
    width: 5px;
    height: 8px;
  }
  .modal__list {
    margin-top: 29px;
  }
  .modal__item {
    border-top: 1px solid #e18298;
    border-bottom: 1px solid #e18298;
    padding-top: 13px;
    padding-bottom: 11px;
  }
  .modal__link {
    margin-left: 21px;
    font-family: $main-font;
    color: $menu-text-color;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(18 / 16);
    &:hover,
    &:focus {
      color: $primary-accent-color;
    }
  }
  
  
  
    // MOBILE 
  
    .hero{
      background-color: $hero-header-bg;
        margin: 0 auto;
   @media screen and (max-width:767px){
    overflow: hidden;
       width: 100%;
       height: 446px; 
   }   
   @media screen and (min-width:768px) {
       width: 768px;
       height: 353px;
  }  
  @media screen and (min-width:1280px) {   
      width: 1280px;
      height: 578px;
  }       
    }
   
    .hero__thumb{
      z-index: 3;
      position: absolute;
   @media screen and (max-width:767px) {
      left: 20px;
      top: 137px;      
   }   
   @media screen and (min-width:768px) {
     left: 34px;
     top: 56px;
   }
   @media screen and (min-width:1280px) {
     top: 84px;
     left: 115px;
   }
  }
    .hero__title{
    font-family: $secondary-font;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $primary-bg-color;
      &-red{
        color: $primary-accent-color; 
      }
      @media screen and (max-width:767px) {
        width: 218px;
        font-size: 26px;
        line-height: 1.15;
        &-red{
          font-size: 48px ;
          line-height: 0.95;
        }
      }
      @media screen and (min-width:768px) {
        width: 162px;
        font-size: 22px;
        line-height: 1.13;
        &-red{
          font-size: 34px;
          line-height: 0.9;
        }
      
    }
    @media screen and (min-width:1280px) {
      width: 277px;
      height: 137px;
      font-size: 38px;
        line-height: 1.15;
        &-red{
          font-size: 58px;
          line-height: 0.89;
        }
    }
    }
    .hero-list{
      display: flex;
      @media screen and (max-width:767px) {
        margin-top: 19px;
      }
      @media screen and (min-width:768px) {
        margin-top: 20px;
      }
      @media screen and (min-width:1280px) {
        margin-top: 25px;
      }
    }
    .hero-list__item + .hero-list__item{
      @media screen and (max-width:1279px) {
        margin-left: 8px;
      }
      @media screen and (min-width: 1280px) {
        margin-left: 15px;
      }
  
    }
    .hero-list__button-red{
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: $main-font;
      font-style: normal;
      font-weight: 400;
      border-radius: 20px;
      color: $primary-bg-color;
      background-color: $primary-accent-color;
      &:hover,
      &:focus{
      background-color:$hover-button-primary-color;
      }
  
      @media screen and (max-width:1279px) {
        width: 62.4px;
        height: 24px;
        font-size: 8px;
        line-height: 1.25;
      }
    @media screen and (min-width:1280px) {
        width: 102px;
        height: 40px;
        font-size: 14px;
        line-height: 1.28;
    
      
    }
    }
    .hero-list__button-white{
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: $main-font;
      font-style: normal;
      font-weight: 400;
      align-items: center;
      border-radius: 20px;
      border:none;
      color: $primary-accent-color;
      background-color: $primary-bg-color;
      
      &:hover,
      &:focus{
      background-color: $hover-button-menu-color;
      }
    @media screen and (max-width:1279px){
      width: 82.8px;
      height: 24px;
      font-size: 8px;
      line-height: 1.25;
    }
    @media screen and (min-width:1280px) {
        width: 138px;
        height: 40px;
        font-size: 14px;
        line-height: 1.28;
    }
    }
    .hero__container-ice-cream{
        position: absolute;
        z-index: 2;
        bottom: 0px;
      @media screen and (max-width:767px) {
        left: 117px;    
        width: 171px;
        height: 410px;
      }
     @media screen and (min-width:768px) {
         
         left: 378px;
