        left: 77px;
        bottom: 45px;
      }
    }
  
    .elipse{
        z-index: 1 ;
        position: absolute;
        border-radius: 50%;
        background-color: $secondary-accent-color;
      @media screen and (max-width:767px) {
          left: 36px;
          bottom: 75px;
          width: 346px;
          height: 344px;
  
      }
      @media screen and (min-width:768px) {
          bottom: 46px;
          left: 307px;
          width: 324px;
          height: 322px;
          
      }  
      @media screen and (min-width:1280px) {
       left: 475px;
       bottom: 75px;
       width: 538px;
       height: 548px; 
      }    
